<template>
  <p-chart :data="lineChartData" id="line" :height="400"></p-chart>
</template>

<script>
  export default {
    data () {
      return {
        lineChartData: {}
      }
    },
    methods: {
      setChartData () {
        this.lineChartData = {
          title: '',
          type: 'line', //  ['line','bar','linebar','pie']
          reverse: false, // data
          columns: [
            {
              name: 'dim', // datakey
              title: '', //
              x: true // x
            },
            {
              name: 'index', // datakey
              title: '' // 
            }
          ],
          data: [
            {dim: '', index: 800},
            {dim: '', index: 600},
            {dim: '', index: 500},
            {dim: '', index: 400},
            {dim: '', index: 500},
            {dim: '', index: 600},
            {dim: '', index: 700}
          ]
        }
      }
    },
    mounted () {
      this.setChartData()
    }
  }
</script>



