<template>
    <p-chart :data="barChartData" id="bar" :height="400"></p-chart>
</template>

<script>
  export default {
    data () {
      return {
        barChartData: {}
      }
    },
    methods: {
      setChartData () {
        this.barChartData = {
          title: '',
          type: 'bar', //  ['line','bar','linebar','pie']
          reverse: false, // data
          columns: [
            {
              name: 'dim', // datakey
              title: '', //
              x: true // x
            },
            {
              name: 'index', // datakey
              title: '' // 
            }
          ],
          data: [
            {dim: '', index: 100},
            {dim: '', index: 200},
            {dim: '', index: 300},
            {dim: '', index: 400},
            {dim: '', index: 500},
            {dim: '', index: 600},
            {dim: '', index: 700}
          ]
        }
      }
    },
    mounted () {
      this.setChartData()
    }
  }
</script>



