<template>
    <p-chart :data="pieChartData" id="pie"></p-chart>
</template>

<script>
  export default {
    data () {
      return {
        pieChartData: {}
      }
    },
    methods: {
      setChartData () {
        this.pieChartData = {
          title: '',
          type: 'pie', //  ['line','bar','linebar','pie']
          reverse: false, // data
          columns: [
            {
              name: 'index1', // datakey
              title: '1' // 
            },
            {
              name: 'index2', // datakey
              title: '2' // 
            },
            {
              name: 'index3', // datakey
              title: '3' // 
            },
            {
              name: 'index4', // datakey
              title: '4' // 
            }
          ],
          data: [
            {index1: 100, index2: 800, index3: 200, index4: 400}
          ]
        }
      }
    },
    mounted () {
      this.setChartData()
    }
  }
</script>



