<template>
  <p-chart :data="lineBarChartData" id="linebar" :height="400"></p-chart>
</template>

<script>
  export default {
    data () {
      return {
        lineBarChartData: {}
      }
    },
    methods: {
      setChartData () {
        this.lineBarChartData = {
          title: '',
          type: 'linebar', //  ['line','bar','linebar','pie']
          reverse: false, // data
          columns: [
            {
              name: 'dim', // datakey
              title: '', //
              x: true // x
            },
            {
              name: 'index1', // datakey
              title: '1', // 
              type: 'line' // type'linebar'
            },
            {
              name: 'index2', // datakey
              title: '2', // 
              type: 'bar' // type'linebar'
            }
          ],
          data: [
            {dim: '', index1: 100, index2: 800},
            {dim: '', index1: 200, index2: 600},
            {dim: '', index1: 300, index2: 400},
            {dim: '', index1: 400, index2: 300},
            {dim: '', index1: 500, index2: 500},
            {dim: '', index1: 600, index2: 700},
            {dim: '', index1: 700, index2: 900}
          ]
        }
      }
    },
    mounted () {
      this.setChartData()
    }
  }
</script>



